.nav__link {
  letter-spacing: .02em;
  padding: 5px 0;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.388;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.nav__link:hover, .nav__link:focus {
  color: #4440f6;
}

.nav__link-current {
  color: #4440f6;
