}

.theme-text.light {
  color: #4440f6;
}

.theme-switcher {
  position: relative;
}

.theme-switcher input[type="checkbox"] {
  display: none;
}

.theme-switcher label {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background-color: #f4f4f4;
  border: .604701px solid #4b48db;
  border-radius: 20px;
  display: block;
  position: relative;
}

.theme-switcher label:after {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #4440f6;
  border-radius: 50px;
  transition: all .2s;
  display: block;
  position: absolute;
  top: 1px;
  left: 1px;
}

.theme-switcher input[type="checkbox"]:checked + label {
  background-color: #4440f6;
  border: .604701px solid #f4f4f4;
}

.theme-switcher input[type="checkbox"]:checked + label:after {
  background-color: #f4f4f4;
  transform: translateX(20px);
}

.theme__dark, .theme__light {
  display: none;
}

@media screen and (min-width: 1280px) {
