//  Потім видалити
.news-card__border {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
// ===============================

.read {
  background-color: #f4f4f4;
  padding-top: 40px;
  padding-bottom: 60px;
  @include tablet {
    padding-top: 48px;
    padding-bottom: 100px;
  }

  @include desktop {
    padding-top: 60px;
    padding-bottom: 210px;
  }
}

.read-title {
  display: flex;
  padding-bottom: 12px;
  border-bottom: 1px solid #929292;
  cursor: pointer;
}

.read-date {
  display: inline-block;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.71;
  letter-spacing: 0.02em;
  color: $text-color-light;

  margin-right: 10px;
}

.read-arrow {
  height: 12px;
  width: 12px;
  border: 2px solid #111321;
  border-width: 2px 2px 0 0;
  transform: rotate(135deg);
  transition: transform 300ms;
}

.read-arrow__active {
  transform: rotate(-45deg);
  // transform: translate(-50%, 50%);
  // transform-origin: bottom;
}

.read-gallery {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 28px;
  padding-top: 28px;
  margin-bottom: 40px;
  @include tablet {
    gap: 30px;
    padding-top: 30px;
    margin-bottom: 48px;
  }
  @include desktop {
    gap: 32px;
    padding-top: 40px;
    margin-bottom: 60px;
  }
}

.active {
  display: flex;
}
